  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

[type="checkbox"] {
  margin-right: 5px;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
}

::selection {
  text-shadow: none;
  color: var(--white);
  background-color: var(--c1) !important;
}

::placeholder {
  color: var(--white) !important;
  opacity: 0.4 !important;
}

iframe {
  max-width: 100%;
}

button:focus,
input:focus,
select:focus,
textarea:focus {
  outline: none !important;
}

button,
input[type="submit"] {
  border: none;
  background-color: transparent;
  cursor: pointer;
  -webkit-appearance: button;
  -moz-appearance: button;
}

textarea {
  resize: none;
}

img {
  max-width: 100%;
  height: auto;
}

table {
  width: 100%;
  max-width: 100%;
  text-align: center;
  border-collapse: separate;
  border-spacing: 0 15px;
}

table th,
table td {
  padding: 10px;
}

table th:first-child,
table td:first-child {
  border-radius: 5px 0 0 5px;
  padding-left: 0;
  overflow: hidden;
  position: relative;
}

table th:last-child,
table td:last-child {
  border-radius: 0 5px 5px 0;
}

table thead {
  position: relative;
  z-index: 1;
  background-color: transparent;
  height: 75px;
}

table thead:after {
  width: 100%;
  height: calc(100% - 15px);
  left: 0;
  top: 0;
  border-radius: 5px;
  position: absolute;
  z-index: -1;
  content: "";
}

table thead th {
  color: var(--white);
}

table thead th:first-child {
  padding-left: 30px;
  text-align: left;
}

table tbody td,
table tbody th {
  color: var(--white);
  background-color: #142442;
}

table tbody td > div {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
}

table tbody td > div img,
table tbody td > div svg {
  margin-right: 5px;
}

pre {
  border: 1px solid;
  border-radius: 4px;
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 0.8em 1.6em;
}

code {
  font-size: inherit;
  margin: 0;
}

kbd {
  font-weight: 700;
  color: var(--title-color);
}

ins {
  background-color: transparent;
  text-decoration: none;
}

q {
  font-style: italic;
  text-decoration: underline;
}

mark {
  background-color: #fcf8e3;
}

hr {
  margin-top: 20px;
  margin-bottom: 20px;
  border: 0;
  border-top: 1px solid #e6e6e6;
}

small {
  font-size: 80%;
  font-weight: 400;
}

